import { comm } from './comm';

const angleRange = {
  "J1": [-180, 180],
  "J2": [-120, 120],
  "J3": [-120, 120],
  "J4": [-180, 180],
  "J5": [-180, 180],
  "J6": [0, 120]
};

class Action {
  constructor(props) {

  }

  // 初始化
  start () {
    comm.send('G2');
  }

  resetAllAxis () {
    comm.send('G00 A0 B0 C0 D0 X0 Y0 Z');
  }

  setRelativeMove () {
    comm.send('G9');
  }

  setAbsoluteMove () {
    comm.send('G9');
  }

  // 相对单轴运动
  move (axis, angle, speed) {
    speed = (speed > 2000) ? 2000 : speed;
    var cmd = 'G01 ' + axis + angle + ' F' + speed;
    comm.send(cmd);
  }

  // 多轴联动控制
  moveAllAxis (as, bs, cs, ds, xs, speed) {
    var cmd = 'G01 A' + as + ' B' + bs + ' C' + cs + ' D' + ds + ' X' + xs + ' F' + speed;
    comm.send(cmd);
  }

  // 设置固件模式
  setMode (mode) {
    let modeMaps = {
      "paishe-open": "M10",
      "paishe-close": "M11",
      "penqi-open": "M10",
      "penqi-close": "M11",
      "jiaqu-open": "M10",
      "jiaqu-close": "M11",
      "xiqu-open": "M10",
      "xiqu-close": "M11",
      "hanjie-open": "M10",
      "hanjie-close": "M11"
    };
    comm.send(modeMaps[mode]);
  }
}

export const action = new Action();



